.container {
    padding-top: 10px;
    box-sizing: border-box;
    // background-color: rgba(13, 224, 70, 0.829);
    border: solid;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;

}

.container_title,
.container_title01 {
    text-align: center;
    position: relative;
    padding-bottom: 6px;
    color: aliceblue;
    height: 40px;
}

.container_title h1 {
    font-family: monospace;
}

.menu_body {

    width: 50px;
    height: 35px;
    border-radius: 50px;
    position: absolute;
    right: 0;
    top: 0;
    background-color: #2a06f8c7;
    font-size: small;
    color: white;
}


.container_note {
    box-sizing: border-box;

    display: flex;
    width: 80%;
    height: 100%;
    position: relative;
    margin: auto;
    flex-direction: column;
}

.contaienr_cuerpo {
    display: flex;
    position: relative;
    height: 100%;
    overflow: hidden;

}

@media screen and(max-width:950px) {
    .container_note {
        width: 90%;
    }
}

@media screen and(max-width:750px) {
    .container_note {
        width: 98%;
    }

    .container_body {
        flex: 3 1;
    }
}

.log_and_menu {
    display: flex;
    position: absolute;
}

.log_and_menu .menu_itemButtom_01,.menu_itemButtom_03 {
    border-radius: 20px;
    border: solid rgb(8, 138, 138);
    background-color: rgb(17, 66, 66);
    margin: 0 5px;
    font-size: large;
    padding: 0 10px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    cursor: pointer;
    color: rgba(243, 238, 236, 0.993);
    margin-left: 10px;
}

.menu_itemButtom {
    border: none;
    border-radius: 50%;
    width: 35px;
    height: 35px;
}

.menu_itemButtom img {
    width: 100%;
}

.menu_itemButtom_02 {
    display: block;
        border-radius: 20px;
        border: solid rgb(8, 138, 138);
        background-color: rgb(17, 66, 66);
        font-size: small;
        padding: 0 5px;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        cursor: pointer;
        color: rgba(243, 238, 236, 0.993);
        margin-left: 5px;
}

@media screen and(max-width:430px) {
    .menu_itemButtom_01 {
        display: none;
    }

    .menu_itemButtom_02 {
        display: block;
        border-radius: 20px;
        border: solid rgb(8, 138, 138);
        background-color: rgb(17, 66, 66);
        font-size: small;
        padding: 0 5px;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        cursor: pointer;
        color: rgba(243, 238, 236, 0.993);
        margin-left: 5px;
    }

    .menu_container{
        display: none;
    }
}

//MENU CONTAINER 
.menu_container{
    width: 100%;
    display: none;
    padding: 10px 0;
    border-right: solid rgb(120, 8, 155);
    position: absolute;
    z-index: 200;
    height: 99%;
    background-color: rgba(43, 16, 16, 0.925);
}

//CONTAINER LIST



.container_list {
    border-radius: 5px 5px 0 0;
    box-sizing: border-box;
    display: block;
    width: 210px;
    min-width: 200px;
    max-width: 800px;

    resize: horizontal;
    scroll-behavior: smooth;
    padding-bottom: 10px;

    background-color: rgba(106, 161, 159, 0.856);

    padding: 1px;

    overflow: hidden;
    padding-top: 25px;
}

.risize_container {
    position: relative;
    width: 2px;
    bottom: 0;
}

.risize_container img {
    position: absolute;
    color: red;
    z-index: 999;
    background-color: white;
    bottom: -5px;
    left: -25px;
    height: 25px;
    width: 30px;
    border-radius: 50px;
}


@keyframes ocultar {
    0% {
        position: relative;
        left: 0;
    }


    100% {
        position: relative;
        left: 100px;
    }
}



@media screen and(max-width:750px) {

    .container_title h1 {
        display: none;
    }

    .container_list {
        display: none;
        position: absolute;
        overflow-x: hidden;
        height: 100%;
        z-index: 101;
        width: 85%;
        resize: none;

    }

}



.cotainer_option_list {
    width: 200px;
    position: absolute;
    top: 0;
    max-width: 210px;
    z-index: 1;
    // display: flex;
}

.cotainer_option_list button {
    // width: 50%;
    margin: auto;
    background-color: rgba(18, 198, 204, 0.644);
    border-radius: 2pc;

}

.cotainer_option_list .añadir {
    padding: 5px;
    font-size: large;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: white;
    background-color: brown;
    border-color: greenyellow;
}

.cotainer_option_list .add_folder {
    font-size: x-large;
    border: solid rgb(2, 48, 48);
    width: 100%;

}

.container_items {
    float: right;
    font-size: 20px;
    width: 100%;
    // padding-top: 25px;

    height: 100%;
    overflow: auto;
}

// ESTILOS DE ESCROLLL
.container_items::-webkit-scrollbar {
    width: 8px;
    /* Tamaño del scroll en vertical */
    height: 8px;
    /* Tamaño del scroll en horizontal */
    // display: none;  /* Ocultar scroll */
}

.container_items::-webkit-scrollbar-thumb {
    background: rgb(139, 9, 9);
    border-radius: 4px;
}

.container_items::-webkit-scrollbar-thumb:hover {
    background: #dd1414;
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

/* Cambiamos el fondo cuando esté en active */
.container_items::-webkit-scrollbar-thumb:active {
    background-color: #ce1010;
}

