.icons {
    width: 50px;
    // height: 100px;
    background-color: aquamarine;
}

.pruebas_items {
    width: 30px;
    background: gray;
}

.items {
    position: relative;
    margin-top: 10px;
    margin-left: 5px;
    height: auto;
    // overflow: hidden;
    display: block;
    border-left: 1px solid rgba(135, 139, 125, 0.459);
    border-color: red;
    border-left-style: dashed;
    min-width: 100px;
}

.items p {

    border-bottom: 2px red;

    color: rgb(11, 24, 97);
    cursor: pointer;
    width: auto;
    white-space: nowrap;
    // text-align: justify;
    text-overflow: ellipsis;
    // overflow: hidden;
    // hyphens: auto;
    font-size: smaller;
    height: 20px;
    padding-left: 27px;
    padding-top: 2px;
    padding-bottom: 5px;
    font-weight: bold;
}

.items .puntos {
    transform: rotate(90deg);
    color: white;
    cursor: pointer;
    float: left;
    padding: 0 2px;
}

.puntos:hover {
    color: rgb(150, 33, 33);
    background-color: antiquewhite;
}

.items p:hover {
    color: rgb(207, 70, 28);

}

.items img,
.items span {
    width: 15px;
    float: left;
    overflow: hidden;
    margin: 0px 0 0 3px;
}

.items .flecha {
    color: white;
}

.items img {
    float: left;
    margin-top: 5px;
    margin-right: 5px;
}

.inputAddFolder {
    margin-top: 5px;
    width: 97%;
    padding: 1px;
    outline: none;
    display: none;
}


.itemsMenu {
    display: none;
    position: absolute;
    z-index: 102;
    background-color: rgb(58, 116, 131);
    color: white;
    top: 0;
    left: 0;

}

.itemsMenu p {
    padding: 5px;
    cursor: pointer;
}

.itemsMenu p:active {
    background-color: aqua;
}

.itemsMenu p:hover {
