// //CONTAINER BODY
.container_body {

    flex: 2 1;
    max-width: 100%;
    min-width: 200px;
    position: relative;
    overflow: hidden;

    background-color: rgba(22, 16, 16, 0.63);
    color: wheat;

}

.button_editables {
    border-radius: 20px 20px 0 0;

    background-color: rgb(22, 16, 16);

    padding: 2px;
    top: -3px;
    right: 0;
    position: absolute;
}

.button_editables button {
    border-radius: 7px;
    border: solid rgb(61, 4, 4);
    box-shadow: cadetblue 5px 2px 20px inset;
    font-size: large;
}

.button_editables p {
    color: greenyellow;
    background-color: rgba(5, 238, 5, 0.425);
    text-shadow: black 2px 2px 3px;
    font-size: large;
    position: absolute;
    padding: 3px;
    font-size: 0;
    animation: 1s ease-in .5s 1 normal none;
}

@keyframes animate {
    50% {
        font-size: 20px;
    }

    90% {
        font-size: 20px;
    }
}

.button_editables button {
    margin: auto;
    margin: 5px 5px;
    padding: 2px;

}

.apuntes {
    padding-top: 5px;
    width: 100%;

    position: absolute;
    height: 100%;
    overflow: hidden;
}

.apuntes .namefolder {
    position: absolute;
    padding: 0px 10px;
    font-size: small;
    font-family: 'Times New Roman', Times, serif;
    color: red;
}

.apuntes textarea {
    outline: none;
    width: 100%;

    color: white;

    background-color: rgba(22, 16, 16, 0);

    max-width: 100%;
    min-width: 300px;
    padding: 15px 5px 5px 5px;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
    vertical-align: baseline;

    bottom: 0;
    resize: none;
    font-size: medium;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    height: 95%;
}

.apuntes textarea::placeholder {
    color: wheat
}